@import "./variables";

@mixin font($font-size: 18px, $line-height: 22px,
            $font-weight: medium, $color: $black) {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: $font-size;
  line-height: $line-height;
  font-style: normal;
  font-weight: $font-weight;
  color: $color;
}


/*===================
  Poly-fluid sizing
===================*/

/*
list-remove
Remove an item from a list
@param $list - A SASS list
@param $index - The list index to remove
@returns A SASS list
@author Jake Wilson <jake.e.wilson@gmail.com>
*/

@function list-remove($list, $index) {
  $newList: ();
  @for $i from 1 through length($list) {
    @if $i != $index {
      $newList: append($newList, nth($list,$i), 'space');
    }
  }
  @return $newList;
}

/*
list-sort
Sort a SASS list
@param $list - A SASS list
@returns A sorted SASS list
@requires function list-remove
@author Jake Wilson <jake.e.wilson@gmail.com>
*/

@function list-sort($list) {
  $sortedlist: ();
  @while length($list) > 0 {
    $value: nth($list,1);
    @each $item in $list {
      @if $item < $value {
        $value: $item;
      }
    }
    $sortedlist: append($sortedlist, $value, 'space');
    $list: list-remove($list, index($list, $value));
  }
  @return $sortedlist;
}

/*
map-sort
Sort map by keys
@param $map - A SASS map
@returns A SASS map sorted by keys
@requires function list-sort
@author Jake Wilson <jake.e.wilson@gmail.com>
*/

@function map-sort($map) {
  $keys: list-sort(map-keys($map));
  $sortedMap: ();
  @each $key in $keys {
    $sortedMap: map-merge($sortedMap, ($key: map-get($map, $key)));
  }
  @return $sortedMap;
}

/*
linear-interpolation
Calculate the definition of a line between two points
@param $map - A SASS map of viewport widths and size value pairs
@returns A linear equation as a calc() function
@example
font-size: linear-interpolation((320px: 18px, 768px: 26px));
@author Jake Wilson <jake.e.wilson@gmail.com>
*/

@function linear-interpolation($map) {
  $keys: map-keys($map);
  @if (length($keys) != 2) {
    @error "linear-interpolation() $map must be exactly 2 values";
  }
  // The slope
  $m: (map-get($map, nth($keys, 2)) - map-get($map, nth($keys, 1)))/(nth($keys, 2) - nth($keys,1));

  // The y-intercept
  $b: map-get($map, nth($keys, 1)) - $m * nth($keys, 1);

  // Determine if the sign should be positive or negative
  $sign: "+";
  @if ($b < 0) {
    $sign: "-";
    $b: abs($b);
  }

  @return calc(#{$m*100}vw #{$sign} #{$b});
}

/*
Generate linear interpolated size values through multiple break points
@param $property - A string CSS property name
@param $map - A SASS map of viewport unit and size value pairs
@requires function linear-interpolation
@requires function map-sort
@example
@include poly-fluid-sizing('font-size', (576px: 22px, 768px: 24px, 992px: 34px));
@author Jake Wilson <jake.e.wilson@gmail.com>
Note: mixin name changed from 'poly-fluid-sizing' to 'sizing'
*/

@mixin sizing($property, $map) {
  // Get the number of provided breakpoints
  $length: length(map-keys($map));

  // Error if the number of breakpoints is < 2
  @if ($length < 2) {
    @error "sizing() $map requires at least values"
  }

  // Sort the map by viewport width (key)
  $map: map-sort($map);
  $keys: map-keys($map);

  // Minimum size
  #{$property}: map-get($map, nth($keys,1));

  // Interpolated size through breakpoints
  @for $i from 1 through ($length - 1) {
    @media (min-width:nth($keys,$i)) {
      #{$property}: linear-interpolation((nth($keys,$i): map-get($map, nth($keys,$i)), nth($keys,($i+1)): map-get($map, nth($keys,($i + 1)))));
    }
  }

  // Maximum size
  @media (min-width:nth($keys,$length)) {
    #{$property}: map-get($map, nth($keys,$length));
  }
}

